<template>
  <div id="blog">
    <scroll class="wrapper" ref="scroll" :probe-type="3" :listen-scroll="listenScroll"
            :pullup="true" :pulldown="true"
            @scroll="ContentScroll" @pullingUp="pullUp" @pullingDown="pullDown">
      <Row type="flex" justify="center" align="top" style="padding-top: 20px;padding-bottom: 20px">
        <Col :xs="{span:24}" :sm="{span:23}" :lg="{span:23}" :xl="{span:15}">
          <div class="banner">
            <div class="title">
              博客&笔记
            </div>
            <p class="contrast">
              站主主攻python web(django、drf、flask)和前端(vuejs、react)的全栈开发方向，副方向打算向数据分析与挖掘这条道路上靠拢。
              全栈所有博客资源大都为站主的学习笔记或学习经验之谈，一来整理起来方便学习巩固，二来也方便可能有需要的大家。
              有时也会学习机器学习、算法方面的内容，如有需要也会一并整理。
              尽是一些拙劣之文，如有错误与不足之处，站主恳请大家不吝赐教。
            </p>
          </div>
        </Col>
        <Col :xs="{span:24}" :sm="{span:23}" :lg="{span:23}" :xl="{span:15}">
          <div class="tags">
            <button class="btn" v-for="tag in tags" :key="tag" @click="getArticles(tag)">{{tag}}</button>
          </div>
        </Col>
        <Col :xs="{span:24}" :sm="{span:23}" :lg="{span:23}" :xl="{span:15}">
          <div class="articles">
            <Row type="flex" justify="center" align="top" style="width: 100%;">
              <Col v-for="article in articles[currentType].list" :key="article.title"
                   :xs="{span:24}" :sm="{span:12}" :lg="{span:8}" :xl="{span:8}" style="display: flex;justify-content: center;">
                <Card :filler="article"></Card>
              </Col>

            </Row>
          </div>
        </Col>
        <Col :xs="{span:24}" :sm="{span:23}" :lg="{span:23}" :xl="{span:15}">
          <div class="pagination">
            <Page :total="100"/>
          </div>
        </Col>
      </Row>
    </scroll>
  </div>
</template>

<script>
  import Card from "components/common/card/Card";
  import Scroll from "components/common/Scroll/Scroll";

  import {getArticlesbyType} from "network/articles";

  export default {
    name: "Blog",
    data(){
      return {
        tags: ['django', 'python', 'vue.js', 'react', 'flask', '数据库', '分布式', '其它'],
        device_width: document.body.clientWidth || document.documentElement.clientWidth,
        showModal: false,
        listenScroll: true, //是否监听scroll
        articles:{
          'django': {page:0, list:[]},
          'python': {page:0, list:[]},
          'vue.js': {page:0, list:[]},
          'react': {page:0, list:[]},
          'flask': {page:0, list:[]},
          'database': {page:0, list:[]},
          'spark': {page:0, list:[]},
          'other': {page:0, list:[]},
        },
        currentType:'django'
      }
    },
    components:{
      Card,
      Scroll
    },
    created() {
      this.getArticlesbyType('django');
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll, true);
      this.$bus.$on('browserChange',(val) => {
        this.device_width = val;
      })
    },
    methods:{
      ContentScroll(position){
        /* 监听移动端touch滚动 */

      },
      handleScroll(){
        /* 监听pc端滚动事件 */
        let scrollHeight = document.documentElement.scrollTop || document.body.scrollTop;

      },
      pullUp(){
        console.log('pullup');
        this.$refs.scroll.finishPullUp();
      },
      pullDown(){
        console.log('pulldown');
        this.$refs.scroll.finishPullDown();
      },

      /* 网络请求相关方法封装 */
      getArticlesbyType(type){
        let page = this.articles[type].page + 1;
        getArticlesbyType(type,page).then(res => {
          this.articles[type].list.push(...res.results);
          this.articles[type].page++;
          console.log(this.articles[type].list);
        });
      },

      getArticles(tag){
        // 先获取数据，再展示数据
        if(tag === '数据库'){
          tag = 'database'
        }else if(tag === '分布式'){
          tag = 'spark'
        }else if(tag === '其它'){
          tag = 'other'
        }
        this.getArticlesbyType(tag);
        this.currentType = tag;
      }
    },
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll);   //  离开页面清除（移除）滚轮滚动事件
    }
  }
</script>

<style scoped lang="less">
  #blog{
    width: 100%;
    height: 100%;
    background: url(../../assets/img/bg3.gif) repeat;
    position: relative;

    @media screen{
      @media (max-width: 568px){
        height: calc(100vh - 44px);
      }
      @media (min-width: 569px) and (max-width: 992px){
        height: calc(100vh - 56px);
      }
      @media (min-width: 993px) and (max-width: 1200px){
        height: calc(100vh - 56px);
      }

    }

    .wrapper{
      height: auto;
      @media screen{
        @media (max-width: 568px){
          position: absolute;
          top: 0;bottom: 0;
          left: 0;right: 0;
        }
        @media (min-width: 569px) and (max-width: 1200px){
          position: absolute;
          top: 0;bottom: 0;
          left: 0;right: 0;
        }
      }
    }

    .banner{
      display: flex;
      flex-direction: column;
      align-items: center;
      .title{
        font-size: 27px;
        font-weight: bold;
        padding: 10px 0;
        letter-spacing: 2px;
      }
      .contrast{
        width: 75%;
        font-size: 14px;
        padding: 5px 20px;
        background: rgba(180,180,180,0.2);
        border-radius: 10px;
        @media screen{
          @media (max-width: 768px){
            width: 90%;
            padding: 5px 10px;
          }
          @media (min-width: 769px) and (max-width: 1200px){
            width: 80%;
          }
        }
      }
    }

    .tags{
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 20px 0;
      flex-wrap: wrap;

      .btn{
        margin: 3px 5px;
        border-radius: 15px;
        padding: 4px 10px;
        background: @color-pink;
        color: @color-white;
        cursor: pointer;
        border: @color-pink solid 1px;

        &:hover{
          background: @color-white;
          color:@color-pink;
        }
      }
    }

    .articles{
      width: 100%;
      padding: 20px 0;
      @media screen{
        @media (max-width: 568px){
          padding: 10px 0;
        }
      }
    }

    .pagination{
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 20px 0;
    }
  }
</style>