<template>
  <div class="card">
    <header class="card-thumb">
      <a v-if="filler.avatar" :href="filler.avatar">
        <img :src="filler.avatar" alt="图片">
      </a>
      <a v-else>
        <img src="~assets/img/avatar-me.jpg" alt="图片">
      </a>
    </header>
    <div class="card-date">
      <span class="card-date-day">19</span>
      <br/>
      <span class="card-date-month">十月</span>
    </div>
    <div class="card-body">
      <div class="card-category"><a href="#">照片</a></div>
      <h2 class="card-title" @click="toRead">{{filler.title}}</h2>
      <div class="card-tags">
        <button v-for="item in filler.tags" :key="item">{{item}}</button>
      </div>
      <p class="card-description">
        <span v-html="description"></span>
      </p>
    </div>

    <footer class="card-footer">
      <i class="fas fa-clock"></i> 10分钟前
      <i class="fas fa-eye"></i><a href="#"> 648 评论</a>
      <button class="unfold" @click="unFold">点击展开</button>
    </footer>
  </div>
</template>

<script>
  export default {
    name: "Card",
    props:{
      filler:Object
    },
    data(){
      return{

      }
    },
    mounted() {

    },
    methods:{
      toRead(){
        this.$router.push({
          name:'blogDetail',
          params:{
            blogId:this.filler.id
          }
        });
      },
      unFold(){

      }
    },
    computed:{
      description(){
        return this.filler.body.substring(0,100) + '...';
      }
    },
  }
</script>

<style scoped lang="less">
  .card {
    width: 95%;
    margin: 10px 0;
    background-color: #fff;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    -webkit-transition: box-shadow 0.5s;
    transition: box-shadow 0.5s;
    position: relative;
    a {
      color: inherit;
      text-decoration: none;
    }

    &:hover {
      box-shadow: 0 0 50px rgba(0, 0, 0, 0.3);
    }

    .card-date {
      position: absolute;
      top: 20px;
      right: 20px;
      width: 45px;
      height: 45px;
      padding-top: 10px;
      background-color: @color-pyblue;
      border-radius: 50%;
      color: #fff;
      text-align: center;
      font-weight: 700;
      line-height: 13px;
    }
    .card-date-day {
      font-size: 14px;
    }
    .card-date-month {
      text-transform: uppercase;
      font-size: 12px;
    }

    .card-thumb {
      height: 250px;
      overflow: hidden;
      background-color: #000;
      -webkit-transition: height 0.5s;
      transition: height 0.5s;
      display: flex;
      align-items: center;

      @media screen{
        @media (max-width: 1200px){
          height: 230px;
        }
      }


      img {
        width: 100%;
        height: auto;
        display: block;
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
        -webkit-transition: opacity 0.5s, -webkit-transform 0.5s;
        transition: opacity 0.5s, transform 0.5s, -webkit-transform 0.5s;
      }
    }

    .card-body {
      position: relative;
      height: 180px;
      padding: 20px;
      -webkit-transition: height 0.5s;
      transition: height 0.5s;

      @media screen{
        @media (max-width: 1200px){
          height: 170px;
        }
      }

      .card-category {
        position: absolute;
        top: -25px;
        left: 0;
        height: 25px;
        padding: 0 15px;
        background-color: @color-pyblue;
        color: #fff;
        text-transform: uppercase;
        font-size: 11px;
        line-height: 25px;
      }

      .card-title {
        margin: 0;
        padding-bottom: 10px;
        color: #000;
        font-size: 22px;
        font-weight: bold;
        text-transform: uppercase;
        cursor: pointer;
        @media screen{
          @media (max-width: 1200px){
            font-size: 16px;
          }
        }
        &:hover{
          color: @color-pink;
        }
      }

      .card-tags {
        button{
          padding: 0 5px;
          background: @color-doulv;
          border: @color-white solid 1px;
          border-radius: 5px;
          color: @color-white;
          margin-right: 5px;
          cursor: pointer;
        }
      }

      .card-description {
        position: absolute;
        left: 20px;
        right: 20px;
        bottom: 56px;
        margin: 0;
        padding: 0;
        color: #666C74;
        line-height: 27px;
        opacity: 0;
        -webkit-transform: translateY(45px);
        transform: translateY(45px);
        -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
        transition: opacity 0.3s, transform 0.3s, -webkit-transform 0.3s;
        -webkit-transition-delay: 0s;
        transition-delay: 0s;
        @media screen{
          @media (max-width: 1200px){
            font-size: 12px;
          }
        }
      }
    }

    .card-footer {
      position: absolute;
      bottom: 12px;
      left: 20px;
      right: 20px;
      font-size: 11px;
      color: #A3A9A2;
      .unfold{
        background: transparent;
        padding: 0 5px;
        color: @color-muted;
        position: absolute;
        @media screen{
          @media (min-width: 1200px){
            display: none;
          }
        }
      }
    }
  }

  .card:hover .card-thumb {
    height: 130px;
  }

  .card:hover .card-thumb img {
    opacity: 0.6;
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }

  .card:hover .card-body {
    height: 300px;
    @media screen{
      @media (max-width: 1200px){
        height: 270px;
      }
    }
  }

  .card:hover .card-description {
    opacity: 1;
    -webkit-transform: translateY(-20px);
    transform: translateY(-20px);
    -webkit-transition-delay: 0.2s;
    transition-delay: 0.2s;
    @media screen{
      @media (max-width: 1200px){
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
      }
    }
  }

</style>